.Container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 50px;
  padding: 20px;
}

.PersonContainer {
  width: 400px;
  height: 300px;
  background-color: white;
  text-align: center;
  border: 1px solid black;
  border-radius: 5px;
  padding: 20px;
}

.PersonImages {
  background-color: brown;
  border-radius: 5px;
}

.PersonEmails {
  font-weight: bold;
}
